import React from 'react'
import Image1 from '../images/gallery/Image1.png'
import Image2 from '../images/gallery/Image2.png'
import Image3 from '../images/gallery/Image3.png'
import Image4 from '../images/gallery/Image4.png'

const Gallery = () =>{
    return(
        <div className="gallery">
            <img className="item" src={Image1} alt="xd znowu rip" />
            <div className="items-column">
                <img className="item" src={Image2} alt="xd znowu rip" />
                <img className="item" src={Image3} alt="xd znowu rip" />
            </div>
            <img className="item" src={Image4} alt="xd znowu rip" />
        </div>
    );
}

export default Gallery