import React from 'react'

const Form = () =>{
    return(
        <div className="form">
            <input type="email" className="form-email" placeholder="email"></input>
            <input type="text" className="form-topic" placeholder="topic"></input>
            <textarea className="form-message" placeholder="message"></textarea>
            <button className="form-send">send</button>
        </div>
    );
}

export default Form